.donation_amount{
    padding: 20px 40px;
    border: 1px solid #a3e4f1;
    border-radius: 15px;
    margin-top: 10px;
}
.donation_amount h2{
    padding-bottom: 12px;
    margin-top: 0px;
    border-bottom: 1px solid #a3e4f1;
    font-weight: 500;
}
.donation_amount form{
    justify-content: left;
    padding-top: 8px;
}
.donation_amount p {
    margin-top: -8px;
    margin-bottom: 20px;
    padding-left: 30px;
    font-size: 14px;
    color: #888787;
}

.second_box{
    margin-top: 30px;
}

